      <!-- <div class="mes">运费：¥{{goods.free}}元</div> -->
    </div>
    <div class="border"></div>

    <!-- 参数选择 -->
    <div class="column">
      <!-- <div class="item flex-between">
        <div class="title">地址</div>
        <div class="text flex_1 ellipsis">广东省 深圳市</div>
        <van-icon name="arrow" class="arrow"/>
      </div>-->
      <van-cell is-link @click="showCoupons=true">
        <template slot="title">
          <span class="custom-text title"
                style="margin-right:0.533333rem">领劵</span>
          <van-tag color="rgba(249, 39, 12, 1)" plain>点击领取优惠券</van-tag>
        </template>
      </van-cell>
      <van-cell>
        <template slot="title">
          <span class="custom-text title">地址 </span>
          <!-- <span class="text">{{address}}</span> -->
          <span class="text"> 广东省--深圳市</span>
        </template>
      </van-cell>
      <!-- <van-cell is-link @click="showsku=true">
        <template slot="title">
          <span class="custom-text title">已选</span>
          <span class="text">{{sku_name}}</span>
        </template>
      </van-cell> -->
      <!-- <van-cell>
        <template slot="title">
          <span class="custom-text title">说明</span>
          <span class="text">广东省深圳市</span>
        </template>
      </van-cell> -->
