      showCoupons: false,


      spec_goods_price: [],   //  规格参数对应的价格与规格id
      hu_guige:'',            //  总规格
      hu_guige_list:[],       //  总规格
      hu_guigestr:'',         //  验证
      key:[],                 //  规格标题
      filter_spec: {},        //  规格
      price: 0,               //  选完规格后的价格
      freight: 0,
      goods: {
        goods_content: ''
      },
      action:'',              //  购买方式   buy_now  直接购买   ""  加入购物车
      tocart:0,               //  选择规格弹出层  判断直接购买还是加入购物车
    }
  },
  created(){
    try{
      if(this.$route.query.type_0==0){
        this.type_0 = true
      }
    }catch(err){}
    console.log(this.$route.query.id)
    this.getdata();
  },
  methods:{
    //  点击领取优惠券
    getCoupon(item,i) {
      get_coupon({ coupon_id: item.id}).then((res)=>{
        if(res.code==200){
          Toast(res.data.msg);
          this.couponList[i].isget=1
        }
        if (res.err) return
      })
    },
    //  去结算
    uporder(){
      if(!this.price){
        Toast('请先选择规格及数量');
        return;
      }
      if(this.tocart){
        this.addcart();     //  加入购物车
        
        return;
      }
      if(this.action){
        this.$router.push({   //  直接购买
          path:'/uporder', 
          query:{
            item_id:this.spec_goods_price[this.hu_guige].item_id,
            goods_num:this.num,
            // price:this.price,
            // img:this.goods_images_list[0].image_url,
            // goods_name:this.goods.goods_name,
            goods_id:this.goods.goods_id,
            action:'buy_now',
            freight: this.freight
          }
        })
      } else {

      }
      
    },
    async getdata(){
      var res = await getdetails({id:this.$route.query.id});
      this.goods_images_list = res.data.goods_images_list
      this.goods = res.data.goods
      this.filter_spec = res.data.filter_spec
      this.spec_goods_price = res.data.spec_goods_price
      this.couponList =  res.data.coupon_list               //  优惠券列表
      this.freight = res.data.freight
    },
    changeColor(item_id, index, key) {
      if(this.hu_guigestr.search(key)==-1&&this.hu_guigestr.search(item_id)==-1){
        this.hu_guigestr +=key+'_'+ item_id+"_"
      } else if(this.hu_guigestr.search(key)!=-1&&this.hu_guigestr.search(item_id)==-1){
        var arr = this.hu_guigestr.split('_')
        for(let i=0;i<arr.length;i++){
          if(arr[i]==key){
            arr[i+1] = item_id;
            this.hu_guigestr = arr.join("_")
            break;
          }
        }
      }
      var list = this.hu_guigestr.split("_")
      this.hu_guige="";
      var arr1=[];
      for(let i=0;i<list.length;i++){
        if(i%2==1){
          arr1.push(Number(list[i]))
        }
      }
      arr1.sort((a,b)=>a-b)
      console.log(arr1)
      this.hu_guige = arr1.join('_')
      console.log(this.hu_guige)
      if(this.spec_goods_price[this.hu_guige]){
        this.price = this.spec_goods_price[this.hu_guige].price
      }
    },
    async addcart(){   //  加入购物车
      var res = await addToCart({
        goods_id:this.goods.goods_id,
        cat_id:this.goods.cat_id,
        goods_num:this.num,
        item_id:this.spec_goods_price[this.hu_guige].item_id,
      })
      if(res.code==200){
        Toast('加入购物车成功');
        this.$router.push({path:'/cart'})
      }
    },
    //  联系客服
    kefu(){
      var that = this;
      if(that.$store.state.kefu){
        Dialog.confirm({
          title: that.$store.state.kefu.title,
          message: that.$store.state.kefu.number
        }).then(() => {
          // 不加回报异常
        }).catch(() =>{
          // 不加回报异常
        })
        return;
      }
      kefu().then(res =>{
        that.$store.state.kefu = res.data
        Dialog.confirm({
          title: res.data.title,
          message: res.data.number
        }).then(() => {
          // 不加回报异常          
        }).catch(() =>{
          // 不加回报异常
        })
      })
    },
  },
}
